.button-to-circle {
  $bgColor: #fafafa;
  $shadow: rgba(lighten($bgColor, 10%),.2);
  $secondaryColor: #C5C5C5;
  $btnWidth: 180px;
  $btnHeight: 40px; // Firefox users - don't touch!
  $borderRadius: 3px;
  $btnBorder: 2px;
  $loaderStroke: 3px;
  $dribbble: #ea4c89;
  $turquoise: #3FC0C2;

  button.btnSubmit{
    // reset
    transition: box-shadow 300ms ease;
    user-select: none;
    height: $btnHeight;

    position: relative;
    transition: all 300ms ease-out;
    &:focus {
      outline: none;
    }
    &:hover {
      cursor: pointer;
    }
    &.clicked {
      pointer-events: none;
      animation: anim1 200ms cubic-bezier(0.2, 0, 0.8, 1), anim2 200ms cubic-bezier(0.6, 0, 0.7, 1) 200ms 1 forwards;
      border: none;
      box-shadow: none;
      &.done {
        display: flex;
        justify-content: center;
        align-items: center;
        width: $btnHeight;
        background: #3FC0C2; // fallback
        background: linear-gradient(0, #3FC0C2, lighten(#3FC0C2, 5%));
        border-width: 0;
        border-color: rgba(#3FC0C2, 0);
        color: rgba(#3FC0C2, 0);
        transition: all 1000ms ease;
        animation: anim4 300ms cubic-bezier(0.2, 0, 0.4, 1) forwards;
      }
    }
    &.error {
      display: flex;
      justify-content: center;
      align-items: center;
      width: $btnHeight;
      background: #F26649;
      border-width: 0;
      border-color: rgba(#3FC0C2, 0);
      color: rgba(#3FC0C2, 0);
      transition: all 1000ms ease;
      animation: anim4 300ms cubic-bezier(0.2, 0, 0.4, 1) forwards;
      .cross path {
        opacity: 1;
        -webkit-transition: stroke-dashoffset 0.3s;
        transition: stroke-dashoffset 0.3s;
      }
    }
  }


  svg.svg--template{
    display: none;
  }
  svg.loader{
    position: absolute;
    width: $btnHeight;
    height: $btnHeight;
    overflow: visible;
  }

  svg{
    &.loader{
      position: absolute;
      left: 68px;
      top: -($btnBorder);
      width: $btnHeight;
      height: $btnHeight;
    }
    &.checkmark{
        height: 1.2em;
        width: 100%;
    }
    .circle1 {
      stroke: #3FC0C2;
      stroke-width: $loaderStroke;
      r: ($btnHeight - $loaderStroke)/2;
      cx: ($btnHeight)/2;
      cy: ($btnHeight)/2;
      width: 100%;
      height: 100%;
      stroke-dasharray: $btnHeight*3.14;
      transform: rotate(-90deg);
      transform-origin: 50% 50%;
      animation: animLoading 2500ms cubic-bezier(0.6, 0, 0, 1);
    }
    .checkmark1 {
      fill: $bgColor;
      .line1 {
        transform-origin: 100% 100%;
      }
      .line2{
        transform-origin: 0% 100%;
      }
      .line1, .line2{
        animation: growAndRotate 500ms cubic-bezier(0.4, 0, 0, 1);
      }
    }
    &.cross{
      height: 100%;
      width: 100%;
      path {
        stroke: #fff;
        stroke-linecap: round;
        stroke-width: 4;
        -webkit-transition: opacity 0.1s;
        transition: opacity 0.1s;
      }
    }
  }

  @keyframes anim1 {
    50% {
      font-size: 0.9em;
    }
  }

  @keyframes anim2 {
    40% {
      border-radius: $borderRadius;
    }
    20%, 100% {
      color: rgba(#3FC0C2, 0);
    }
    100% {
      background: none;
      border-width: $btnBorder;
      border-color: $secondaryColor;
      border-radius: 50%;
      padding: 0;
      width: $btnHeight;
    }
  }

  @keyframes animLoading {
    0%{
      stroke-dashoffset: $btnHeight*3.14;
    }
    100%{
      stroke-dashoffset: 0;
    }
  }

  @keyframes anim4 {
    0% {
      border-radius: 50%;
      width: $btnHeight;
    }
    40% {
      border-radius: $borderRadius;
    }
    100% {
      width: auto;
    }
  }
  @keyframes growAndRotate {
    0% {
      opacity: 0;
      transform: scale(.4) rotate(-5deg);
    }
    30%, 100% {
      opacity: 1;
    }
    100% {
      transform: scale(1) rotate(0);
    }
  }
}
