.waivers{
  margin-right: 20px;
  border: none;

  .radiogroup [role='radio'] {
    margin-right: 20px;
    font-weight: 100;
    display: inline-block;
  }

  .new-waiver {
    margin-top: 40px;
  }
  h4 {
    font-size: 20px;
  }
  table.waiver-list {
    tr {
      border-bottom: 1px solid #E1E3E7;
      &.waiver-row {
        &:hover{
          background: #EFF0F2;
        }
      }
    }
    a.action {
      color: #1D2D47;
      margin-right: 50px;
      &:hover {
          color: #149B9E;
      }
    }
  }

  form {
    input.form-control {
      padding-left: 10px;
    }
    label, .waiver-actions {
      margin-top: 30px;
    }
    .waiver-actions {
      margin-bottom: 80px;
    }
    .waiver-number-expiration {
      display: inline-block;
      padding-right: 10%;
      width: 40%;
    }
    .waiver-expiry-date-field {
      -webkit-appearance: menulist;
      text-indent: 10px;
      cursor: pointer
    }
    .airport-code {
      border-bottom: 1px solid #E1E3E7;
      padding-bottom: 20px;
      padding-left: 0;
      margin-left: 15px;
      input {
       width: 240px;
      }
    }

    input[type='submit'] {
      padding: 8px 20px;
    }
  }
}
