.mission-payment-view {
  .header-row {
    border-bottom: 1px solid lightgray;
    margin-bottom: 5px;
  }

  .no-card {
    color: #adadad;
  }

  .mission-payment {
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid lightgray;
    padding-bottom: 5px;
    margin-bottom: 5px;

    div {
      flex-grow: 1;
      width: 50%;
    }

    div:nth-of-type(even) {
      text-align: right;
    }

    .timestamp {
      font-size: 10px;
      color: #adadad;
    }

    .payment-info {
      color: #337ab7;
      font-size: 10px;
    }

  }

  .mission-payment:last-of-type {
    border-bottom: 0;
    padding-bottom: 0;
    margin-bottom: 0;
  }
}
