.drones-devices-equipment {

  .accordion {
    border-bottom: 1px solid #BDBDBD;
    padding: 14px 0;
  }

  .accordion-first {
    border-top: 1px solid #BDBDBD;
  }


  .equipment-header {
    font-size: 16px;
    cursor: pointer;
    margin-bottom: 0;

    .arrow {
      img {
        width: 10px;
      }
    }

    .counter {
      color: #ccc
    }

  }

  .equipment-form {
    padding-top: 9px;
  }

  .clear-button a {
    color: $db-turquoise-1;
    font-size: 12px;
  }
  .checkbox-filter {
    margin-top: 6px;
    input[type="checkbox"] {
      margin-right: 5px;
    }
  }

}
