.asset-uploader {
  margin-bottom: 20px;

  .upload-actions {
    margin-bottom: 20px;

    .progress-text {
      font-size: 20px;
    }
  }

  .asset {
    border: 1px solid #ccc;
    position: relative;
    padding: 8px 10px;
    margin-bottom: 2px;


    .title {
      padding-top: 5px;
      padding-bottom: 5px;
      font-weight: 200;
      i {
        font-size: 18px;
      }
      a {
        i {
          color: #D03131;
          font-size: 20px;
        }
      }
      label {
        margin-right: 10px;
        input {
          height: auto;
        }
      }
    }

    .progress {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: -1;
      margin-bottom: 0;
      height: auto;
      border-radius: 0;
      background-color: #FFF;
      box-shadow: none;

      .progress-bar {
        box-shadow: none;
        background-color: rgba(51, 122, 183, 0.40);
      }
    }
  }
}

.video-placeholder {
  margin-bottom: 20px;
  width: 100%;
}
