.page-header.edit-badge{
    padding-left: 24px;
}

.license-list {
    display: inline-block;

    label {
        margin-left: 20px;
    }
}

.page-header.badges{
  margin-right: 20px;
  border: none;
  .new-badge {
    margin-top: 20px;
  }
}

table.badge-list {
    tr {
        border-bottom: 1px solid #E1E3E7;
        &.badge-row {
            &:hover{
                background: #EFF0F2;
            }
        }
    }
    a.action {
        color: #1D2D47;
        margin-right: 50px;
        &:hover {
            color: #149B9E;
        }
    }
    td.requirements {
        font-style: italic;
        color: #8D95A2;
        width: 35%;
        padding: 5px;
        p {
            margin: 0;
        }
    }
}

.refresh-mindflash{
    margin-left: 2px;
    span{
        color: #149B9E;
        font-size: 12px;
        text-decoration: underline;
        padding-top: 2px;
    }
    img {
        width: 18px;
        height: 18px;
    }
}
.edit-badge .badge-col {
    margin-top: 30px;
}

.col-lg-12.badge-col {
    padding-left: 40px;
    padding-right: 40px;
}

.badges {
  .automation-switch {
    padding-top: 10px;
    label {
      margin-bottom: 0;
    }
  }

  .filter-button {
    margin: 0 0 40px 0;
  }
}

.col-md-4.badge-col {
    h4 {
        font-size:  20px;
    }
    font-family: "Roboto";
    padding-left: 40px;
    padding-right: 40px;
    &.right-seperator {
        border-right: 3px solid #eeeeee;
        min-height: 300px;
    }
    p, label, li {
        padding: 0;
        color: #1D2D47;
        font-size: 14px;
        font-weight: 300;
    }

    b, ul li:first-child{
    margin-top: 10px;
    }

    input[type='text'], select {
        border-radius: 3px;
        height: 40px;
        font-size: 16px;
        padding-left: 12px;
        color: #ababab;
        &.changed {
            color: black;
        }
    }
}
.save-badge {
  margin-left: 28px;
  margin-bottom: 400px;
  height: 36px;
  input {
    padding: 10px 25px;
  }
}

#badge-dispatch-requirement-modal {
  .geo {
    font-size: .8em;
    padding-top: 5px;
    text-align: right;
  }

  .more-locations, .remove-location {
    font-size: 14px;
    color: #149B9E;
    line-height: 30px;
  }
}


.badge-requirement-modal-content label {
  font-weight: normal;

  input[type="checkbox"] {
    background: #3FC0C2;
  }
}

.badges {

  [data-ember-action] {
    cursor: default;
  }

  .badge-summary {
    
    margin: 0 10px 20px 0;

    .filter-summary, .target-summary {
      background-color: #EFF0F2;
      min-height: 130px;
      padding: 20px;
      font-size: 13px;
    }

    .target-summary {
      border-left: solid 5px #fff;

      .pilot-count {
        font-size: 30px;
      }
      .pilot-count-label {
        font-size: 24px;
      }

    }

    .filter-summary {
      ul.main {
        margin: 0;
        padding: 0;
        list-style: none;

        ul.items {
          margin: 0;
          padding: 0;
          display: inline;
          list-style: none;

          li {
            display: inline;
          }

          li:after {
            content: ", ";
          }

          li:last-child:after {
              content: "";
          }
        }
      }
    }

  }
}

