.weather-header {
  padding: 5px 0;
  border-bottom: 3px solid #eee;
  margin-bottom: 15px;

  span {
    font-size: 22px;
  }
}

.weather-icon-canvas {
  opacity: 0.5;
}

.mission-weather {

  .mission-weather-header {
    margin-bottom: 15px;
    width: 100%;

    span {
      padding: 5px 0;
    }
    .sunset{
      float: right;
      text-align: right;
      color: #8D95A2;
      font-size: 14px;
    }

    a {
      color: #8D95A2;
    }

    a:hover {
      text-decoration: none;
      color: #3FC0C2;
    }

    .playback-icon {
      font-size: 18px;
      vertical-align: text-bottom;
    }

    .arrow-icon {
      font-size: 28px;
      vertical-align: sub;
    }
  }

  .forecast {
    color: #99a1ab;
    padding: 15px 10px;
    margin-right: -5px;
    text-align: center;
    display: inline-block;
    border: 1px solid #eee;
    border-right: 0;

    canvas {
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }

  .mission-weather-content {
    font-size: 12px;

    div:last-child {
      margin-right: 0;
      border: 1px solid #eee;
    }
  }

}
