.processes-label {
  font-size: 14px;
  color: #1D2D47;
  line-height: 30px;
  font-weight: 500;
}
.processes-table {
  font-size: 12px;
  color: #1D2D47;
  tr td {
    padding: 5px !important;
    line-height: 25px !important;
  }
  .process-status {
    font-style: italic;
    text-transform: capitalize;
    &.error {
      color: #F26649;
    }
  }
  .process-timestamp {
    color: #8D95A2;
  }
}
