.table-responsive {
  padding-bottom: 75px;
  overflow-x: inherit;
  &.no-padding-bottom {
    padding-bottom: 0;
  }
}

table {
  .dropdown-menu {
    right: 0;
    left: auto;
    text-align: center;

    li {
      margin: 4px;
    }
  }

  th.sortable-column {
    cursor: pointer;

    i{
      color: #CCC;
    }

    &.active {
      background-color: transparent !important;
      i {
        color: #000;
      }
    }
  }

  .expandable-tbody {
    cursor: pointer;

    tr:last-child {
      display: none;
    }

    .toggle-indicator {
      font-size: 12px;
      width: 20px;
    }
  }
  tr.row-link {
    &:hover {
      background-color: #f1f1f1;
    }
  }
}
.dronebase-table-header {
  font-size: 12px;
}
