.badge-modal {
  font-family: "Roboto";
  span {
    i {
      display: block;
    }
  }
  .green-checkmark {
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }
  &.full-screen-modal{
    width: 60%;
  }
  .btn-primary, .btn-secondary, .btn-warning {
    display: inline-flex;
  }

  .btn-primary, .btn-warning {
    margin-left: 15px;
  }

  .btn-primary.disabled {
    background: #C5C9D0;
    border: 1px solid #C5C9D0;
    &:hover {
      pointer: not-allowed;
      text-decoration: none;
    }
  }

  .requirement-wrapper {
    margin-left: 20px;
    p {
      font-weight: 500;
      font-size: 14px;
      margin-bottom: 4px;
      color: #8D95A2;
    }
    .fa-caret-right {
      width: 14px;
      height: 14px;
      color: #8D95A2;
    }
    .requirement {
      font-weight: 200;
      margin-left: 14px;
      font-size: 16px;
      margin-bottom: 10px;
      color: black;
    }
  }


  .edit-badge {
    margin-left: 0;
    width: 130px;
    margin-top: 20px;
  }

  .badge-selection{
    margin-top: 15px;
    margin-bottom: 5px;
    select {
      margin-top: 5px;
      font-size: 16px;
      height: 40px;
      &.changed {
        color: black;
      }
    }
    label {
      font-size: 14px;
      font-weight: 300;
    }
  }
}
