.dropdown-refine {
  width: 265px;
  margin-top: 20px;

  .refine-header {
    .fa {
      opacity: .75;
    }
    font-family: roboto;
    background-color: #3fc0c2;
    height: 30px;
    color: white;
    padding-top: 5px;
    padding-left: 20px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    display: flex;
    .header-title {
      width: calc(100% - 25px);
    }
  }
  .refine-content {
    background-color: white;
    color: black;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    padding: 10px;
    padding-left: 20px;
    padding-right: 20px;
    label {
      font-size: 14px;
      font-family: roboto;
      font-weight: 200;
    }
    input {
      margin-right: 5px;
    }
  }
}
