html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: roboto;
}

body {

  .busy-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 9999;

    .loading-container {
      border-radius: 5px;
      background-color: white;
      width: 300px;
      height: 100px;
      position: fixed;
      top: 50%;
      left: 50%;
      margin-left: -150px;
      margin-top: -50px;
    }

    .processing {
      position: absolute;
      top: 0;
      left: 0;
      line-height: 100px;
      width: 300px;
      padding-left: 150px;
      font-size: 16px;
    }

    .loader, .loader:after {
      border-radius: 50%;
      width: 7em;
      height: 7em;
    }
    .loader {
      margin: 60px auto;
      font-size: 10px;
      position: relative;
      text-indent: -9999em;
      border-top: 1.1em solid rgba(70,192,193, 0.2);
      border-right: 1.1em solid rgba(70,192,193, 0.2);
      border-bottom: 1.1em solid rgba(70,192,193, 0.2);
      border-left: 1.1em solid #46c0c1;
      -webkit-transform: translateZ(0);
      -ms-transform: translateZ(0);
      transform: translateZ(0);
      -webkit-animation: load8 1.1s infinite linear;
      animation: load8 1.1s infinite linear;
      top: -45px;
      left: -76px;
    }
    @-webkit-keyframes load8 {
      0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
      }
      100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
      }
    }
    @keyframes load8 {
      0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
      }
      100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
      }
    }
  }

  &.busy {
    overflow: hidden;
  }

  [aria-checked="true"][role="checkbox"]::before {
    background-image: url(images/admin_organization/checked_box.svg);

    & {
      border: 0;
    }
  }

  h4 {
    font-family: roboto;
    color: $db-blue;
    font-size: 15px;
  }
  i.loadable {
    display: none;
  }

  i.loadable-input {
    color: #238BE4;
  }

  &.loading {
    tbody.loadable {
      opacity: 0.3;
    }

    i.loadable-hide {
      display: none;
    }

    i.loadable {
      display: inline;
    }
  }

  .container-main {
    padding: 0 30px 0;
    .main-row {
      margin-right: -6px;
    }
  }

  [data-ember-action] {
    cursor: pointer;
  }
  .page-header {
    h1, h2, h3, h4 {
      margin: 0;
    }
  }
  .error {
    color: $brand-danger;
  }
  input.error {
    border-color: $brand-danger;
  }
  .error-border {
    border: 1px solid $brand-danger;
  }
  .note {
    color: $gray;
    font-size: 13px;
  }
  input.form-control, textarea.form-control {
    // color: #000;
  }

  .well .well-info {
    margin-bottom: 10px;
  }

  .d-inline-block {
    display: inline-block;
  }

  .v-hidden {
    visibility: hidden;
  }

  .mb-2 {
    margin-bottom: 5px;
  }

  .mb-3 {
    margin-bottom: 10px;
  }

  .ml-3 {
    margin-left: 10px;
  }

  .ml-5 {
    margin-left: 20px;
  }

  .mr-5 {
    margin-right: 20px;
  }

  .top-buffer {
    margin-top: 10px;
  }

  .top-buffer-lg {
    margin-top: 20px;
  }

  .bottom-buffer {
    margin-bottom: 10px;
  }

  .bottom-buffer-lg {
    margin-bottom: 20px;
  }

  .modal-overlay {
    z-index: 1060;
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.3);
  }

  .modal-content {
    z-index: 1061;
    position: absolute;
    top: 20px;
    left: 50%;
    background-color: #fff;
    padding: 1em;
    width: 800px;
    margin-left: -400px;
    max-height: 100%;
    overflow-y: scroll;
    .page-header {
      margin-top: 0;
    }
  }

  .modal-toprow {
    .title {
      display: inline-block;
    }
    .close-modal {
      margin-top: 20px;
      width: 20px;
    }
  }

  .ember-modal-overlay.translucent {
    z-index: 1060;
    background-color: rgba(0,0,0,0.50);
  }
  .ember-modal-dialog {
    padding: 0;
    border-radius: 2px;
    box-shadow: 0 0 3px #222;

    .modal-footer {
      border-top: 0;
    }

    h4 {
      font-weight: 200;
    }

    @media (min-width: $screen-sm-min) {
      min-width: 675px
    }
  }
  #pac-input:focus {
    border-color: #4d90fe;
  }

  .pac-container {
    font-family: Roboto;
    z-index: 2000;
  }

  .notice {
    background: #E8FFE8 !important;
  }

  .header-button {
    margin-top: 20px;
  }
  .nav-tabs > li:hover, .nav-tabs > li.active:hover > a{
    cursor: pointer;
  }

  .full-screen-modal {
    .close-modal {
      img {
        margin-top: 15px;
        width: 20px;
      }
    }

    top: 0;
    right: 0;
    left: initial;
    border-radius: 0;
    width: 70%;
    min-height: 100%;
    box-shadow: none;
    height: 100%;
    overflow: auto;

    @media(min-width: 2000px) {
      width: 50%;
    }

    .title {
      margin: 20px 0 30px;
    }
    .full-screen-modal-toprow {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }
  }
}

.navbar {
  margin: 0;
  a {
    cursor: pointer;
  }
  ul.dropdown-menu.pilot-menu {
    left: 0;
  }
  .selected-menu {
    font-weight: bold;
    color: #149B9E !important;
  }

}

.breadcrumb {
  margin-top: 10px;
}

.switch-with-label {
  display: flex;
  align-items: center;
  .switch {
    margin-left: 10px;
  }
}
.switch {
  position: relative;
  display: inline-block;
  width: 32px;
  height: 14px;
}

/* Hide default HTML checkbox */
.switch input {display:none;}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #C5C9D0;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: -2px;
  bottom: -2px;
  background-color: #F1F1F1;
  -webkit-transition: .4s;
  transition: .4s;
  box-shadow: rgba(0,0,0,.24) 0px 2px 2px;
}

input:checked + .slider:before {
  background-color: #3FC0C2;
  left: -8px;
}
input:checked + .slider {
  background-color: rgba(63, 192, 194, .4);
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

// make the dropdown appear on hover instead of click
.dropdown-topnav:hover .dropdown-menu {
    display: block;
}

.section-header {
  font-size: 18px;
  font-weight: 600;
}
.no-padding {
  padding: 0;
}
.no-padding-right {
  padding-right: 0;
}

.radiogroup {

  [role="radiogroup"],
  [role="radoigroup"] *::after,
  [role="radiogroup"] *::before {
    box-sizing: border-box;
  }

  [role="radiogroup"] {
  }

  [role="radio"] {
    padding-left: 2.5rem;
    position: relative;
    cursor: pointer;
    line-height: 25px;
    vertical-align: middle;
  }

  [role="radio"]::before {
    content: '';
    position: absolute;
    left: 0rem;
    top: 4px;
    width: 16px;
    height: 16px;
    border: 2px solid #8D95A2;
    border-radius: 50%;
  }

  [role="radio"]::after {
    content: '';
    position: absolute;
    left: 4px;
    top: 8px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
  }

  [role="radio"][aria-checked="true"]::before {
  }

  [role="radio"][aria-checked="true"]::after {
    background-color: rgba(70,192,193, 1);
  }

  :focus {
    outline: none;
  }


}

.turquoise-link {
  color: #00c3c4;
  &:hover {
    text-decoration: underline;
  }
}
