.client-details-view {
  .profile-link {
    float: right;
    font-size: 12px;
  }
  .header-row {
    border-bottom: 1px solid lightgray;
    margin-bottom: 5px;
    padding-bottom: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
    .name {
      margin: 10px 0 0 0;
    }
  }
  .body-row {
    font-family: roboto;
    color: $db-blue;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .invoiceable {
      font-size: 12px;
      color: $gray;
    }
    .login-as-customer-link {
      font-size: 11px;
      margin-top: 8px;
    }
  }
}
