.email-notify {
  .button-success {
    display: inline-block;
    border: 1px solid #F26649;
    width: 110px;
    text-align: center;
    color: #F26649;
    text-decoration: none;
    font-weight: 500;
    padding: 5px 0;
    text-transform: uppercase;
    font-size: 12px;
    border-radius: 3px;
  }

  .button-success:hover {
    background: #F26649;
    color: white;
  }

  .cancel {
    margin-left: 15px;
    font-weight: 500;
    color: $db-blue;
    text-transform: uppercase;
    font-size: 12px;
  }

  .cancel:hover {
    color: #F26649;
    text-decoration: none;
  }

  .success-message {
    span {
      font-weight: 500;
      font-size: 16px;
    }

    img {
      width: 25px;
      margin-bottom: 5px;
    }
  }



  margin-bottom: 10px;

}
