.new-org-modal {
  .buttons-block{
    margin-top: 30px;
  }
  label {
    margin-bottom: 5px;
    margin-top: 20px;
    font-weight: 200;
  }
  .organization-inputs {
    input {
      width: 450px;
      border-radius: 3px;
      height: 40px;
    }

    button {
      display: inline-block;
    }
  }
}
.organization {
  font-family: roboto;
  .page-header {
    border-bottom: none;
    .pull-right {
      text-align: right;
    }
  }
  .new-org-button {
    margin-top: 20px;
  }

  .org-table {
    .org-table-header {
      display: flex;
      border-bottom: 2px solid #eee;
      padding-bottom: 5px;
      font-size: 12px;
      .name, .contact, .created {
        font-weight: bolder;
      }
    }
    .org-table-item {
      display: flex;
      padding-top: 10px;
      padding-bottom: 10px;
      border-bottom: 1px solid #eee;
    }
    .name {
      margin-left: 20px;
    }
    .view {
      margin-right: 20px;
    }
    .name, .created {
      width: 20%;
    }
    .contact {
      width: 60%;
    }
    .view {
      width: 50px;
    }
  }
}

.organization-page {
  font-family: roboto;
  .table-header {
    font-size: 20px;
    font-weight: 900;
    margin: 20px 0;
    .pretable-note {
      font-size: 14px;
      font-weight: 100;
      margin-top: 10px;
      max-width: 600px;
      line-height: 20px;
    }
  }
  .organization-name {
    font-size: 26px;
    line-height: 34px;
    padding-bottom: 18px;
    border-bottom: 2px solid #eee;
    margin-bottom: 20px;
  }
  .edit-data {
    display: flex;
    align-items: flex-end;
    margin-bottom: 30px;
    label {
      font-family: roboto;
      font-weight: lighter;
      font-size: 14px;
    }
    input {
      border-radius: 3px;
      padding: 10px 16px;
      font-size: 16px;
      height: 40px;
      padding-left: 12px;
    }
  }
  .org-name-input, .org-slug-input {
    width: 400px;
    margin-right: 30px;
    margin-bottom: -1px;
    color: #a3a3a3;
    &.changed {
      color: #000;
    }
  }
  .organization-packages {
    margin-top: 40px;
    &.first {
      margin-top: 0px
    }
  }
  .package-list-header {
    display: flex;
    border-bottom: 2px solid #eee;
    padding-bottom: 5px;
    font-weight: 900;
  }
  .visibility {
    width: 50px;
    img {
      width: 20px;
    }
  }
  .vertical, .name {
    width: 15%;
  }
  .price {
    width: 8%;
  }
  .slug {
    width: 15%;
  }
  .instructions {
    width: 20%;
  }
  .template {
    width: 20%;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .open > .dropdown-menu {
    display: block;
    right: 0;
    left: auto;
    text-align: center;
  }

  .package-list-item {
    display: flex;
    border-bottom: 1px solid #eee;
    padding: 16px 0;
  }
  .no-package {
    color: #8D95A2;
    margin-top: 20px;
  }
  .btn-primary {
    margin-top: 30px;
  }

  .darkblue {
    color: #1D2D47
  }
  .total-clients {
    color: #8D95A2;
  }
  .turquoise-text {
    color: #149B9E
  }
  .add-client-form {
    label {
      color: #8D95A2;
      font-weight: normal;
    }
    .new-client-emails {
      margin-bottom: 30px;
    }
    .button-to-circle {
      width: 90px;
    }
    .error-message {
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 30px;
    }
  }
}
.organization-clients {
  td {
    padding: 15px 0 !important;
    vertical-align: middle !important;
  }
  .org-owner-managment {
    .remove-from-owners, .add-as-owner {
      btn {
        margin-left: 20px;
      }
      i.account-icon {
        display: inline-block;
        height: 25px;
        width: 25px;
        background-image: url(/assets/images/admin_organization/owner_icon.svg);
        background-size: contain;
        background-repeat: no-repeat;
        vertical-align: middle;
        margin-right: 5px;
      }
    }
    .add-as-owner {
      visibility: hidden;
    }
  }
  .row-link:hover {
    background-color: rgba(63, 192, 194, 0.25);
    .org-owner-managment {
      .remove-from-owners, .add-as-owner {

      }
      .add-as-owner {
        visibility: visible;
      }
    }
  }

}

/*
organization page
*/

@keyframes gonezo {
  to { width: 0; height: 0; overflow: hidden; }
}

.organization-clients {
  span[class*="client-added-"] {
    display: none;
    float: right;
    margin-right: 40px;
  }

  .toast-added {
    display: block !important;
    animation: gonezo 0s ease-in 5s forwards;
  }
}
