.mission-editing-section.mission-schedule {
  .header {
    display: flex;
    justify-content: space-between;
    padding: 15px 5px;
  }
  .save-schedule{
    margin-top: 24px;
  }
  input.disabled {
    pointer-events: none;
    -webkit-appearance: none;
    text-indent: 0;
  }
  .mission-duration {
    margin: 8px 0 6px;
  }
}

.mission-schedule {
  .schedule-mission-date-field {
    input {
      -webkit-appearance: menulist;
      text-indent: 10px;
      cursor: pointer
    }
  }
  .input-group.time, .schedule-mission-time-field {
    width: 100%;
  }
  label.schedule {
    margin-bottom: 0;
  }
}
