.pe-modal-close {
  margin: 15px;
  float: right;

  img {
    width: 15px;
  }
}

.pe-modal-content {
  padding: 30px 40px;
  color: #444c61;

  label {
    font-size: 16px;
  }

  .error-message {
    margin: 5px 0;
    color: #f26649;
  }

  .checkbox-label {
    font-weight: normal;
  }

  .camera-list {
    margin-left: -15px;
    margin-right: -15px;
    max-width: 400px;
  }

  .camera-pill {
    border: 1px solid $db-torquiose;
    display: inline-block;
    padding: 5px;
    border-radius: 15px;
    color: $db-torquiose;
  }

  .form-group-padding-fix {
    padding-left: 30px;
    padding-right: 30px;
  }

  h3 {
    margin-top: -5px;
    border-bottom: 1px solid #bbb;
    padding-bottom: 10px;
  }

  textarea {
    height: 100px;
    border-radius: 3px;
    border: 1px solid #ccc;
  }

  input[type="text"] {
    border-radius: 4px;
  }

  input[type="radio"] {
    margin-right: 5px;
  }
}
