body.missions-map {
  padding-bottom: 0;

  .ember-view {
    height: 100%;
  }
  .container-main {
    padding-top: 0;
  }
  .container-main, .row:nth-of-type(1), .google-map-height-adjust, .google-map {
    height: 100%;
  }
  nav {
    height: 50px !important;
  }
}

#mission_map_plan {
  position: relative;
  #map_canvas {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    .gm-tilt {
      display:none !important;
      // hack to hide the tilt control.
    }
  }

  @keyframes fade-in {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  #map_ui_container {
    display: none;
    width: 561px;
    margin-left: 15px;
    @media (max-width: $screen-xs-min) {
      width: 100%;
      margin-left: 0;
    }
    .row {
      margin-left: 0;
      margin-right: 0;
    }
    #feature-buttons {
      display: block;
      @media (max-width: $screen-xs-min) {
        display: none;
      }
      padding-left: 18px;
      width: 565px;
      .feature {
        float: left;
        margin: 10px 5px 0 5px;
        font-size: inherit;
        font-weight: 200;
        height: inherit;
        text-align: center;
        border-radius: 3px;
      }
      .help {
        float:left;
        margin: 10px 5px 0 5px;
        border-radius: 3px;
        font-size: inherit;
        color: #999;
        background-color: #eee;
      }
      .enabled {
        animation-name: fade-in;
        animation-delay: 0.3s;
        animation-duration: 1s;
        animation-fill-mode: forwards;
      }
      button {
        opacity: 0;
      }
    }

    @keyframes action-buttons {
      from {
        margin-left: -66px;
      }
      to {
        margin-left: 0;
      }
    }

    .enabled {
      animation-name: action-buttons;
      animation-delay: 3s;
      animation-duration: 1s;
      animation-fill-mode: forwards;
    }

    #action-buttons {
      display: block;
      @media (max-width: $screen-xs-min) {
        display: none;
      }
      position: absolute;
      top: 0;
      left: 500px;
      z-index: 1;
      margin-left: -66px;
      button {
        font-size: 40px;
        height: 79px;
        box-shadow: 0 0 0;
      }
    }
    #search-location {
      padding-left: 15px;
      position: relative;
      width: 500px;
      z-index: 2;
      @media (max-width: $screen-xs-min) {
        width: 100%;
        padding-right: 15px;
      }

      input {
        width: 100%;
      }

      .search-drawer {
        background-color: #eee;
        height: 25px;
      }

      .search-field-group {
        position: relative;
        .search-field {
          height: 54px;
          padding-right: 50px;
          box-shadow: 0 0 0;
          @media (max-width: $screen-xs-min) {
            height: 39px;
            padding-right: 30px;
            padding-left: 10px;
          }
        }

        .search-locate {
          position: absolute;
          right: 10px;
          top: 10px;
          font-size: 24px;
          border: none;
          background-color: #FFF;
          color: #C7C7C7;
          @media (max-width: $screen-xs-min) {
            top: 4px;
            right: 0;
            font-size: 20px;
          }

          &:hover {
            color: #b3b0b0;
          }

          &:focus {
            outline: none;
          }
          i, button {
            margin: 0;
            padding: 0;
          }
        }
        .plan-list {
          display: block;
          position: absolute;
          left: 10px;
          top: 10px;
          font-size: 24px;
          border: none;
          background-color: #FFF;
          color: #C7C7C7;
          @media (max-width: $screen-xs-min) {
            display: none;
          }

          &:hover {
            color: #b3b0b0;
          }

          &:focus {
            outline: none;
          }
          &:disabled {
            color: #eee;
            cursor: not-allowed;
          }
          i, button {
            margin: 0;
            padding: 0;
          }
        }
      }

      .map-client-url {
        padding: 4px;
        text-align: right;
        input {
          width: 255px;
          font-size: 10px;
          height: 17px;
        }
      }

      .map-search-undo {
        color: #999;
        padding: 5px 10px;
        float: left;
        display: none;
        max-height: 25px;
        a {
          font-weight: bold;
        }
      }
    }

    .map-locate-button {
      height: 54px;
      background-color: #ECECEC;
      i {
        color: #AFAFAF;
      }
    }
  }

  .controls {
    margin-top: 15px;
    border: 1px solid transparent;
    border-radius: 0;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    outline: none;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
    width: 100%;
    text-align: left;
    height: 48px;
    font-size: 18px;
    @media (max-width: $screen-sm-min) {
      height: 30px;
      font-size: 14px;
    }
    @media (max-width: $screen-xs-min) {
      height: 25px;
      font-size: 12px;
    }
    .features {
      margin: 10px;
    }
  }

  #map_tabs_container {
    display: block;
    position: absolute;
    width: 500px;
    overflow-y: auto;
    top: 145px;
    left: 0;
    z-index: 9;
    margin-left: 15px;
    padding-left: 15px;
    .tab {
      display: block;
      width: 50%;
      background-color: #888;
      padding: 10px;
      color: white;
      text-align: center;
      font-size: 18px;
      font-weight: 300;
      float: left;
      border-radius: 10px 10px 0 0;
    }
    .selected {
      background-color: white;
      color: black;
    }
  }

  #map_shot_list_container {
    display: block;
    position: absolute;
    width: 500px;
    overflow-y: auto;
    top: 185px;
    left: 0;
    bottom: 30px;
    z-index: 9;
    margin-left: 15px;
    padding-left: 15px;
    @media (max-width: $screen-xs-min) {
      width: initial;
      padding-right: 15px;
      margin-left: 0;
    }
    ul {
      position: relative;
      margin: 0;
      padding: 0;
      list-style-type:none;
      width: 100%;
      outline: none;
      .close-help {
        float: right;
        font-size: 11px;
        padding-top: 7px;
        color: #999;
      }
      li {
        cursor: pointer;
        padding: 15px;
        font-size: 14px;
        background-color: white;
      }
    }
    #mission_shot_list {
      li {
        border: none;
        height: initial;
      }

      #shot_list {
        overflow: auto;
        overflow-y: scroll;
        border: 1px dashed #eee;
        margin-top: -29px;
        .title {
          font-size: 16px;
          font-weight: bold;
          .shot-link {
            float: right;
            font-weight: normal;
            font-size: 12px;
            line-height: 16px;
          }
          padding-bottom: 5px;
          border-bottom: 1px solid #ccc;
          margin-bottom: 5px;
        }
        .shot-type-description {
          font-size: 12px;
          line-height: 15px;
          margin-bottom: 15px;
          word-break: break-word;
          letter-spacing: .5px;
        }
        .instructions-label {
          font-size: 12px;
          color: #aaa;
          margin-bottom: 5px;
        }
        .CodeMirror {
          min-height: 100px;
          height: 100px;
          margin-bottom: 5px;
          overflow-y: scroll;
        }
        .CodeMirror-scroll {
          min-height: 70px;
        }

        .shot-actions {
          margin-top: -20px;
          .btn:first-child {
            margin-right: 12px;
          }
        }
      }

      #template_manager {
        margin-top: -10px;
        .template-link {
          float: right;
          margin-right: 0px;
          font-size: 12px;

        }
        .template-form {
          display: none;
        }
      }
    }
  }

  #map_list_container {
    display: none;
    position: absolute;
    width: 500px;
    overflow-y: auto;
    top: 185px;
    bottom: 30px;
    left: 0;
    z-index: 9;
    margin-left: 15px;
    padding-left: 15px;
    @media (max-width: $screen-xs-min) {
      width: initial;
      padding-right: 15px;
      margin-left: 0;
    }
    #map_list {
      display: block;
      margin-bottom: 10px;
    }
    #map_list_help {
      display: block;
    }
    ul {
      position: relative;
      margin: 0;
      padding: 0;
      list-style-type:none;
      width: 100%;
      outline: none;
      .close-help {
        float: right;
        font-size: 11px;
        padding-top: 7px;
        color: #999;
      }
      li {
        cursor: pointer;
        padding: 15px;
        font-size: 14px;
        background-color: white;
        border-left: 15px solid white;
        border-bottom: 1px solid white;
        &.help {
          cursor: inherit;
          border-left-color: white;
          border-bottom-color: #eee;
          i {
              margin-right: 0;
              color: #C7C7C7;
              font-size: 18px;
          }
          .icon {
            padding: 2px 6px 4px;
            border-radius: 2px;
            &.property-area {
              background-color: $brand-primary;
            }
            &.point-of-interest {
              background-color: $brand-warning;
            }
            &.continue {
              background-color: $brand-success;
              i {
                font-size: 13px;
              }
            }
            i {
              font-size: 11px;
              color: white
            }
          }

        }
        &.help-title {
          cursor: inherit;
          border-bottom-color: #eee;
          background-color: #eee;
          border-left-color: #eee;
          line-height: 3px;
          i {
            font-size: 16px;
            color: #999;
            margin: 0 3px 0 -19px;
          }
          b {
            color: #999;
            font-weight: 200;
            font-size: 16px;
          }
        }
        .title {
          color: #eee;
          position: absolute;
          left: 183px;
          top: 2px;
          font-weight: 200;
        }
        .map-list-item {
          display: none;
          margin-left: 30px;
          textarea {
            height: inherit;
          }
          .sample-videos {
            color: #999;
            font-size: 12px;
            margin: -10px -15px 0 0;
          }
          .controls {
            padding: 11px;
            border: 1px solid #eee;
            box-shadow: none;
            font-size: 14px;
          }
        }
        .map-list-shots {
          padding: 15px 0 0 30px;
          .hr {
            border-bottom: 1px dashed #999;
            margin: 25px 4px;
          }
          .map-list-shot {
            padding-bottom: 30px;
            textarea {
              margin-top: 0;
            }
          }
        }
        &.property-area {
          border-color: $brand-primary;
        }
        &.point-of-interest {
          border-color: $brand-warning;
        }
        &.no-fly-zone {
          border-color: $brand-danger;
        }
        i {
          margin-right: 15px;
        }
        .badge {
          background-color: $brand-warning;
        }
      }
    }
  }

  #small_device_controls_container {
    display: none;
    @media (max-width: $screen-xs-min) {
      display: block;
    }
    margin: 15px 0 15px 15px;
    // width has to be set in order for map infowindow to show correctly on small screens.
    width: 100%;
    .help {
      color: #999;
      background-color: #eee;
    }
    button {
      padding: 5px 10px;
      font-size: 15px;
      line-height: inherit;
      border-radius: 0;
      margin-right: 10px;
    }
  }

  #control_boundry_zoom {
    display: none;
    button {
      padding: 7px 15px;
      font-size: 22px;
      line-height: 1.5;
      border-radius: 25px;
      margin-bottom: 25px;
      color: #A7A5A5;
    }
  }

  #controls_container {
    width: 300px;
    margin-left: 15px;
    @media (max-width: $screen-sm-min) {
      width: 140px;
    }
    @media (max-width: $screen-xs-min) {
      width: 110px;
    }
    .btn {
      position: relative;
      padding: 0 5px 0 38px;
      margin-left: 0;
      @media (max-width: $screen-sm-min) {
        padding: 0 5px 0 20px;
      }
      @media (max-width: $screen-xs-min) {
        padding: 0 5px 0 14px;
      }

      i {
        line-height: inherit;
        position: absolute;
        top: 9px;
        left: -25px;
        padding: inherit;
        vertical-align: middle;
        @media (max-width: $screen-sm-min) {
          top: 6px;
          left: -15px;
        }
        @media (max-width: $screen-xs-min) {
          top: 4px;
          left: -11px;
        }
      }
    }
  }

  #info-window-node {
    font-size: 12px;
    textarea {
      border: 1px solid #EEE;
      background-color: #EEE;
      padding: 3px 5px;
      width: 100%;
      @media (max-width: 376px) {
        width: inherit;
      }
      &:focus {
        outline-color: transparent;
        outline-style: none;
      }
    }
    .shots {
      width: 120px;
    }
  }
}
