.asset-images, .asset-panoramas {
  padding-left: 8px;
  padding-right: 8px;


  .processing, .list {
    margin-bottom: 10px;

    .inline {
      margin-right: -8px;
      margin-left: -8px;
    }
  }
}

.asset-panoramas {
  .pano-title {
    float: left;
    margin-right: 10px;
  }
  .pano-share {
    float: left;
  }
}

.asset-videos {
  margin-bottom: 15px;
}

.asset {
  &.deleting-asset {
    opacity: 0.4;

    .actions {
      display: none !important;
    }
  }

  .image {
    position: relative;
    overflow: hidden;
    padding-bottom: 100%;
    border: 3px solid #D6D6D6;

    .img-responsive {
      position: absolute;
      display: block;
      max-width: none;
      max-height: 100%;
      height: auto;
    }

    &.asset-final {
      border-color: #27BD15;
      background-color: #E7FFF3;
    }
  }

  &.visual {
    padding-left: 8px;
    padding-right: 8px;
    padding-bottom: 16px;

    .actions {
      opacity: 0;
      background-color: rgba(0, 0, 0, 0.16);
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      text-align: right;
      padding: 2px 10px;
      margin: 0 8px;

      .asset-share, .promote-asset {
        float: left;
        margin-left: 10px;
      }

      a {
        color: #fff;
        font-size: 20px;
        transition: opacity .3s ease-in-out;
        -moz-transition: opacity .3s ease-in-out;
        -webkit-transition: opacity .3s ease-in-out;

        &.download-asset {
          float: left;
        }
      }
    }

    &:hover {
      .actions {
        opacity: 1.0;
        transition: opacity .3s ease-in-out;
        -moz-transition: opacity .3s ease-in-out;
        -webkit-transition: opacity .3s ease-in-out;
      }
    }
  }

  &.inline {
    position: relative;
    border: 3px solid #D6D6D6;
    padding: 5px 10px;
    margin-bottom: 3px;
    line-height: 20px;

    .title {
      float: left;
      color: #000;
    }

    .asset-viewer {
      margin: -5px -10px -15px;

      &.image {
        margin-bottom: -5px;

        img {
          width: 100%;
        }
      }
    }

    .actions {
      opacity: 0;
      float: right;
      font-size: 20px;

      .asset-share {
        float: left;
        margin-right: 10px;
      }

      &.viewing {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        padding: 5px 8px;

        a {
          color: #fff;

          &.delete-asset {
            float: right;
          }
        }
      }

      a {
        margin-right: 5px;
        transition: opacity .3s ease-in-out;
        -moz-transition: opacity .3s ease-in-out;
        -webkit-transition: opacity .3s ease-in-out;
      }
    }

    &:hover {
      background-color: #e6e6e6;
      cursor: pointer;

      .actions {
        opacity: 1.0;
        transition: opacity .3s ease-in-out;
        -moz-transition: opacity .3s ease-in-out;
        -webkit-transition: opacity .3s ease-in-out;

        &.viewing {
          background-color: rgba(0, 0, 0, 0.16);
        }
      }
    }
  }

  &.asset-final {
    border-color: #27BD15;
    background-color: #E7FFF3;
  }
}

.pswp__caption__center {
  text-align: center;
  font-size: 18px;
  color: white;
}

.pswp-meta {
  float: right;
  margin-top: 88px;
  width: 15%;
  label {
    display: block;
    font-size: 16px;
    margin-bottom: 0;
  }
  .warnings{
    margin-top: 50px;
    .asset-warning {
      text-align: left;
      margin-top: 4px;
      i {
        margin-right: 3px;
      }
      .meta-warning {
        text-decoration: none;
        color: #fec331;
      }
    }
  }
  label, .meta {
    color: white;
  }

  .meta {
    margin-bottom: 10px;
  }
}

.asset-share {
  .disabled, .disabled i {
      color: #CCC;
      text-decoration: none;
    }
}
