.creative-mission {
  .creative-mission-meta-container, .creative-mission-response-container {
    margin-bottom: 20px;
    .creative-mission-meta-title {
      color: $title-blue;
      font-size: 16px;
      font-weight: 500;
      line-height: 1.1;
      margin-bottom: 10px;
      &.rejected {
        color: $brand-danger
      }
    }
    .creative-mission-tag-count{
      position: absolute;
      right: 19px;
      top: 0;
      font-size: 10px;
      color: red;
    }
  }
}
