@import "./mission-edit/schedule.scss";
@import "./mission-edit/asset-processes.scss";

table.airspace-info {
  width: 100%;
  thead {
    font-weight: bold;
    text-align: center;
  }

  tbody {
    .laanc, .type {
      text-align: center;
      width: auto;
    }
  }
}

.mission-flight-app {
  margin-right: 5px;

  a:hover {
    text-decoration: none;
  }

}

.mission-service-button {
  padding-left: 9px;
  .btn, .next a, a {
    font-size: 16px;
    display: inline-block;
    padding: 0;
    letter-spacing: 0;
    font-weight: normal;
    color: #149B9E;
    text-transform: capitalize;
    text-align: left;
    &:hover {
      text-decoration: none;
    }
  }
  .btn-lg {
    display: block;
  }
  .arrow-wrapper {
    vertical-align: middle;
  }
  .arrow-wrapper, .text-wrapper {
    display: inline-block;
    white-space: initial;
  }
}

.mission-id {
  font-weight: 400;
  font-size: 20px;
  margin-bottom: 7px;
}
.mission-reference-id {
  font-size: 14px;
  font-family: roboto;
}

.button-reshoot {
  border: 1px solid #f26649;
  color: #f26649;
}

.button-zendesk {
  color: #8E96A3;
  border: 1px solid #8E96A3;
}

.is-reshoot-box {
  background: #fff9e4;
}

.has-reshoot-box {
  background: #fcdfdb;
}

.is-reshoot-box, .has-reshoot-box {
  border-radius: 3px;
  padding: 10px 15px;
  margin-bottom: 10px;
  color: #444c61;

  a {
    color: #43a8a9;
  }

  a:hover {
    color: #1d2d47;
  }

  span {
    float: right;
  }

  .table-label {
    font-weight: bold;
  }

  .table-content {
    padding-left: 10px;
  }

  .email-notify {
    display: inline-block;
    margin: 0;

    .button-success {
      border: 0;
      margin-left: 25px;
      padding: 3px 10px;
      width: inherit;
      text-transform: none;
    }

    .success-message {
      margin-left: 25px;
    }
  }
}

.mission-pilot-paid {
  padding: 10px 0 10px 16px;
  font-size: 18px;
  display: inline-block;
  color: $brand-success;
  line-height: 1.7;
}
.check-all-raw {
  margin: 0 10px 5px;
  cursor: pointer;
}

.cancel {
  color: $brand-danger
}

.mission-reload {
  margin-left: 5px;
}

.creative-mission-meta-container {
  select {
    border-radius: 0px;
    -webkit-appearance: none;
    padding: 0 12px;
  }
}

.collaboration {
  .btn-primary {
    min-width: 100px;
  }

  .create-mode {
    opacity: 1;
    -webkit-transition: opacity 0.15s linear;
    -moz-transition: opacity 0.15s linear;
    -o-transition: opacity 0.15s linear;
    transition: opacity 0.15s linear;
    &.create-mode-off {
      opacity: 0;
    }
  }

  .collaborator-module {
    .name {
      width: 40%;

      input {
        width: 40%;
        display: inline;
      }
    }

    .email {
      width: 40%;

      input {
        width: 90%;
      }
    }

    .action {
      text-align: center;
      width: 20%;
    }
  }
}
.mission-edit-location-textarea {
  height: 90px !important;
}
.localtime {
  font-size: 10px;
  position: absolute;
  right: 20px;
  top: 2px;
  color: #ababab;
}

.header-text {
  font-size: 18px;
  margin-top: 40px;
  color: #777;
}

.search-field {
  font-weight: normal;
  font-size: 14px;
  color: $db-blue;
  padding: 6px 0 4px;
}

.labels {
  display: flex;
  margin-top: 10px;
  margin-bottom: 20px;
  .label-header {
    margin-right: 20px;
    font-weight: bold;
  }
}

.pilot-results-header {
  display: flex;
  justify-content: space-between;
  .invite-button {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    .num-selected-pilots {
      margin-right: 20px;
    }
    .btn-primary, .btn-warning {
      margin-right: 16px;
    }
    .green-checkmark {
      width: 20px;
      height: 20px;
      margin-right: 10px;
    }
  }
}

.btn-group.zendesk-tickets {
  .dropdown-toggle {
    background-color: white;
    color: #8E96A3;
    &:hover {
      background-color: #8D95A2;
      color: white;
    }
  }
  a {
    padding: 6px 12px;
  }
}
.on-hold-section {
  height: 100px;
  .header {
    padding: 12px;
    label {
      width: 50%;
    }
  }
  div {
    line-height: 1;
    background-color: #FFF9E4;
  }
  label {
    width: 100px;
  }
  .resume-link {
    padding-bottom: 12px;
    padding-top: 5px;
  }
}

.page-actions-header {
  button {
    margin-right: 8px;
  }
}
.mission-status-triggers .btn {
  padding: 10px 0;
}

div.disabled {
  a {
    color: #CCC;
    cursor: not-allowed;
  }
}
.mission-editing-section {
  padding: 15px;
  .header {
    width: 100%;
    padding: 5px;
    font-size: 20px;
    border-bottom: 1px solid $gray;
    margin-bottom: 5px;
  }
}

p.dispatch-info {
  margin-left: 20px;
  font-size: 14px;
}

.rating-panel {
  padding: 15px;
  background-color: #EAF8F8;
  margin-top : -15px;
  p {
    display: inline-block;
    margin: 0;
    padding-top: 5px;
    margin-right: 10px;
  }
}
.small-star {
  margin-top: -2px;
  width: 14px;
  height: 14px;
}

.next {
  width: 150px;
  &.disabled {
    color: #ccc;
    cursor: not-allowed;
  }
  i {
    margin-right: 10px;
  }
  a {
    font-family: 'roboto';
    font-size: 18px;
  }
}
.scheduleTimeDatePicker .error-message {
  color: $brand-danger;
}
.breadcrumb {
  &.need-refresh {
    background-color: #FDEEEB;
  }
  p {
    color: #777777;
    font-size: 14px;
    margin-right: 4px;
  }
  .viewers{
    margin-top: -6px;
  }
  .admin-viewing {
    padding: 6px;
    color: white;
    font-family: 'roboto';
    margin-left: 4px;
    background: lightgrey;
    &.current {
      background: grey;
    }
  }
}
.pilot-instructions {
  .search-field {
    margin: 0;
  }
  .note {
    font-style: italic;
    margin-bottom: 5px;
    .turquoise-edit {
      font-style: normal;
    }
  }
}

.assets-meta {
  line-height: 30px;
  width: 100%;
  color: #1D2D47;
  .col-md-6 {
    padding: 10px 0;
    line-height: 20px;
    label {
      padding-right: 6px;
      margin-bottom: 0;
    }
  }
  .header-row {
    border-bottom: 1px solid lightgray;
    margin: 5px 0;
    padding: 0;
    display: inline-block;
    a {
      display: inline-block;
      padding: 0 4px;
    }
  }
  .asset-share {
    display: inline-block;
  }
  .regenerate-zip {
    float: right;
    font-size: 12px;
    color: $gray;
    padding-left: 20px;
    font-style: italic;
    .buttons-block {
      margin-left: 10px;
      display: inline-block;
      float: right;
      display: flex;
      flex-flow: row no-wrap;
      .btn-primary {
        display: inline-block;
        margin-right: 10px;
        padding: 8.5px 20px;
      }
    }
    .btn-info {
      display: inline-block;
      .spinner {
        display: inline-block;
        width: 15px;
        height: 15px;
      }
    }
  }
}

.shotlist-help {
  width: 100%;
  font-size: 14px;
  color: #8D95A2;
  border-top: 1px solid lightgray;
  padding: 10px 0;
  margin: 5px 0;
  display: inline-block;
}

.activity-logs {
  margin-top: 20px;
}

p.polygon-notes{
  border-top: 2px solid #eee;
  margin-top: 10px;
  padding-left: 0;
}
