.clients {
  .add-package-btn {
    padding-bottom: 45px;
  }
  .organization-packages-table {
    padding-bottom: 45px;
    .edit-organization {
      display: inline-block;
      color: $db-turquoise-2;
      font-size: 18px;
      font-weight: 600;
    }
    .no-packages-note {
      color: #ababab;
      line-height: 19px;
      margin-top: 10px;
      .edit-organization {
        font-size: 14px;
      }
    }
  }
}
.client-page {
  .page-header{
    margin-bottom: 10px;
    font-weight: 600;
  }
  .profile-link {
    display: inline-block;
    margin-left: 7px;
    color: $db-turquoise-2;
  }
  .client-profile {
    margin-bottom: 20px;
    line-height: 22px;
    font-weight: 300;
    .login-as-customer-link {
      font-size: 12px;
      margin-bottom: 20px;
      a {
        color: #149B9E;
      }
    }
  }
  .info-group {
    label {
      display: block;
      color: #89919E;
      font-weight: 300;
      margin-bottom: 0;
      font-size: 12px;
      line-height: 12px;
    }
    &:first-child {
      margin-bottom: 12px;
    }
  }
}
div.client-package-checkbox, div.organization-checkbox {
  width: 15px;
  height: 15px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  &.available {
    background-image: url('images/admin_organization/checked_box.svg');
    &.disabled {
      pointer-events: none;
      background-image: url('images/admin_organization/checked_box_disable.svg');
    }
  }
  &.not-available {
    background-image: url('images/admin_organization/empty_checkbox.svg');
    &.disabled{
      pointer-events: none;
    }
  }
}
