.dropdown-menu.pilot-actions{
  padding: 5px;
  .pilot-action-btn {
    width: 100%;
    text-transform: uppercase;
    color: white;
  }
}
.dropdown-menu.pilot-actions > li > a {
  width: 100%;
  color: white;
  width: 100%;
  margin: 6px 0;
  padding: 6px 20px;
  line-height: 1;
  &.btn-primary:hover {
    background-color: #149B9E;
  }
  &.btn-danger:hover {
    background-color: #D33A1A;
  }
  &.btn-info {
    color: #8D95A2;
  }
}