.pilot-dispatch-row {
  .pilot-name {
    font-size: 18px;
  }

  .field-label {
    color: $db-blue;
    font-weight: 500;
  }

  .full-profile-link {
    font-size: 12px;
    color: #149B9E;
    text-decoration: underline;
  }

  .email-input {
    width: 100%;
    border: 0;
  }

  .pilot-score {
    font-size: 12px;
    color: $gray;
  }

  .invite-info {
    color: #8E96A3;
  }
  .pilot-details {
    display: flex;
    .checkbox-spacing {
      width: 15px;
      margin-right: 10px;
      
    }
  }

  .disable-from-click {
    cursor: default;
  }

}
