.base-button {
  font-size: 15px;
  line-height: 20px;
  font-family: roboto, arial, sans-serif;
  font-weight: 400;
  letter-spacing: 1.5px;
  text-align: center;

  width: fit-content;
  padding: 10px 25px;

  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.16); // == box-shadow: X0 Y2 B1;

  border-radius: 3px;
  border: none;
  text-transform: uppercase;
  &:hover {
    text-decoration: none;
  }
  &:focus {
    outline: none;
  }
  a {
    color: white;
    text-decoration: none;
  }
}

//COLORS
.btn-primary {
  @extend .base-button;
  color: white;
  background-color: #3fc0c2;
  &:hover {
    background-color: #149B9E;
  }
  &:focus, &:active, &:active:focus, &:active:hover {
    color: white;
    background-color: #3fc0c2;
    border: none;
  }
}

.btn-secondary {
  @extend .base-button;
  color: #149B9E;
  background-color: white;
  border: 1px solid #149B9E;
  padding: 9px 25px;
  &:hover, &:focus, &:active {
    color: white;
    background-color: #3FC0C2;
    border-color: #3FC0C2;
  }
}
input[disabled].btn, .btn-primary.disabled, .btn-secondary.disabled, .btn-info.disabled {
  background-color: #C5C9D0;
  color: white;
  border-color: #C5C9D0;
  &:hover {
    background-color: #C5C9D0;
    color: white;
    border-color: #C5C9D0;
    pointer-events: none;
    cursor: disabled;
  }
}
.btn-info {
  @extend .base-button;
  color: #8D95A2;
  background-color: white;
  border: 1px solid #8D95A2;
  padding: 9px 25px;
  &:hover, &:active, &:focus, &:active:focus, &:active:hover {
    color: white;
    background-color: #8D95A2;
    border-color: #8D95A2;
    outline: none;
  }
}
.btn-danger {
  @extend .base-button;
  color: white;
  background-color: #F26649;
  &:hover, &:active, &:focus, &:focus, &:active:focus, &:active:hover {
    background-color: #D33A1A;
  }
}
.btn-warning {
  @extend .base-button;
  color: #F26649;
  background-color: white;
  border: 1px solid #F26649;
  padding: 9px 25px;
  &:hover, &:active, &:focus, &:focus, &:active:focus, &:active:hover {
    color: white;
    background-color: #F26649;
    border-color: #F26649;
  }
}

//OLD BUTTONS
.filled-button {
  @extend .base-button;
  color: white;
}

.turquoise-button {
  @extend .filled-button;
  background: #3fc0c2;
  text-transform: uppercase;

  border: 1px solid #3fc0c2;
  &:hover, &:visited, &:focus {
    background: #3fc0c2;
    color: white;
    text-decoration: none;
  }
  &:hover {
    background: #149B9E;
    border: 1px solid #149B9E;
  }
}

.turquoise-border-button {
  @extend .base-button;
  color: #3fc0c2;
  background: white;

  border: 1px solid #3fc0c2;

  &:hover, &:visited, &:focus {
    background: #3fc0c2;
    color: white;
    text-decoration: none;
    outline: 0;
  }
  &:hover {
    background: #149B9E;
    border: 1px solid #149B9E;
  }
}

//END OF OLD BUTTONS

// SIZES
.btn-base {
  font-family: 'roboto';
  letter-spacing: 1px; // this equals 50
  border-radius: 3px;
}
.btn-lg {
  @extend .btn-base;
  font-size: 16px;
  line-height: 16px;
  padding: 17px 30px;
  &.btn-secondary, &.btn-info, &.btn-warning {
    padding: 16px 30px;
  }
}

.btn-md {
  &.btn-secondary, &.btn-info, &.btn-warning {
    padding: 9px 25px;
  }
}

.btn-sm {
  @extend .btn-base;
  font-size: 13px;
  line-height: 13px;
  padding: 8.5px 16px;
  letter-spacing: 0.75px;
  &.btn-secondary, &.btn-info, &.btn-warning {
    padding: 7.5px 20px;
  }
}
.btn-xs {
  @extend .btn-base;
  padding: 5.5px 10px;
  font-size: 9px;
  line-height: 9px;
  letter-spacing: 0.75px;
  &.btn-secondary, &.btn-info, &.btn-warning {
    padding: 4.5px 10px;
  }
}

.buttons-block {
  .btn:first-child {
    margin-right: 16px;
  }
}
