.payment-modal {
  text-align: center;
  display: flex;
  flex-direction: column;
  padding: 50px;
  align-items: center;
  color: #1d2d47;
  .modal-body {
    position: relative;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 420px;
    font-size: 16px;
    font-weight: 300;
    ul {
      text-align: left;
    }
  }
  .modal-header {
    border-bottom: none;
    font-size: 24px;
  }
  .buttons {
    display: flex;
    margin-top: 40px;
    button {
      margin-left: 10px;
      margin-right: 10px;
      height: 45px;
    }
  }
  .buttons-no-margin-top {
    margin-top: 0;
  }
  img {
    width: 180px;
    margin: 30px;
  }
}
