$db-error: #f26649;

.form {
  h2 {
    margin-bottom: 20px;
    text-align: center;
  }

  textarea {
    height: 250px;
  }
}

// Inputs
input.form-control {
  height: 40px;
  border-radius: 3px;
  padding: 10px 16px;
  font-size: 16px;
  color: $db-blue;
  &.error {
    border-color: $db-error;;
  }
}

//input-groups
.input-group {
  input.form-control {
    border-radius: 0 3px 3px 0 !important;
  }
  &.error {
    .input-group-addon {
      border-color: $db-error;
    }
    input {
      border-color: $db-error;
      border-left-color: #ccc;
    }
  }
}
.form-group-inplace {
  position: relative;

  .field-label {
    position: absolute;
    font-size: 10px;
    left: 14px;
    top: 3px;
  }

  input, textarea {
    padding-top: 16px;
  }

  input {
    min-height: 40px
  }
}

i.search-clear {
  font-size: 16px;
  color: #CCC;
}

.form-group-iconized {
  input {
    padding-left: 30px;
  }

  i {
    position: absolute;
    top: 0;
    padding: 13px 9px;
  }

  i.search-clear {
    right: 16px;
  }
}

//Selects
.select-custom {
  @extend input.form-control;
  font-weight: 300;
  width: 100%;
  text-indent: 6px;
  border: 1px solid #ccc;
  background-color: #fff;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  -webkit-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  -o-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  &:focus {
    outline: 0;
  }
}

textarea.form-control {
  border-radius: 0;
  color: $db-blue;
}

.field-label {
  font-size: 14px;
  color: #ababab;
  font-weight: 400;
}


.inline-input, .inline-textarea, .inline-info {
  position: relative;
  padding: 15px;
  border-bottom: 1px dashed lightgray;

  -webkit-transition: all .5s ease;
  -moz-transition: all .5s ease;
  transition: all .5s ease;

  &.success {
    @keyframes flashsuccess {
      0%   {background-color: transparent;}
      25%  {background-color: yellow;}
      50%  {background-color: transparent;}
      75%  {background-color: #feffc0;}
      100% {background-color: transparent;}
    }

    background-color: transparent;
    animation-name: flashsuccess;
    animation-duration: 1s;
  }

  .placeholder {
    color: lightgray;
  }

  i {
    position: absolute;
    top: 13px;
    right: 10px;
    color: #000;
    opacity: 0;

    -webkit-transition: opacity .3s ease;
    -moz-transition: opacity .3s ease;
    transition: opacity .3s ease;
  }

  &:hover {
    background-color: #feffc0;

    i {
      opacity: 0.3;
    }
  }
}

.inline-textarea {
  height: max-content;
}

.inline-info {
  padding: 15px;

  span {
    color: lightgray;
  }

  &:hover {
    cursor: default;
    background-color: transparent;
  }
}

.form-login {
  button {
    width: 100%;
  }
}

.sso {
  display: block;
  text-align: center;
  padding: 5px;
  border: 1px solid #ccc;
  margin: 15px 0;
  font-size: 18px;
  height: 46px;
  color: #ccc;
  text-decoration: none;
}
.form-group.error {
  input, select {
    border-color: $brand-danger;
  }
}
select.error {
  border-color: $brand-danger
}

.link {
  color: #337ab7;
  &:hover {
    text-decoration: underline;
  }
}
.form-group .error-message {
  color: $brand-danger;
  font-size: 14px;
}