.mission-map {
  #map_canvas {
    height:300px;
  }
  .formatted-address {
  margin-left: 15px;
  margin-bottom: 15px;
    .address {
      font-weight: 200;
      font-size: 16px;
    }
    .lat-long {
      color: #8D95A2;
      font-size: 16px;
    }
    .edit-location {
      margin-top: 10px;
      font-size: 13px;
      color: #149B9E;
      cursor: pointer;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  .search-field-group {
    position: relative;
    .search-field {
      height: 35px;
      border-radius: 2px 0 0 2px;
      box-sizing: border-box;
      -moz-box-sizing: border-box;
      outline: none;
      width: 100%;
      text-align: left;
    }

    .search-locate {
      position: absolute;
      top: 2px;
      right: 1px;
      bottom: 2px;
      font-size: 22px;
      width: 50px;
      border: none;
      background-color: #FFF;
      color: #C7C7C7;

      &:hover {
        color: #b3b0b0;
      }

      &:focus {
        outline: none;
      }
    }
  }
  .col-md-3.kml {
    padding-left: 5px;
  }
  .kml-container {
    display: flex;
    align-items: center;
    align-content: space-between;
  }
  .or {
    display: flex;
    align-items: center;
    align-content: center;
    height: 35px;
    width: fit-content;
    margin-right: 24px;
  }
}
