.onboarding-list {
  ul.dropdown-menu {
    margin-top: 0;
    left: -24px;
    min-width: 190px;
    text-align: left;
  }
  .total-count{
  	margin-right: 15px;
  	padding-left: 56px;
  	margin-bottom: 15px;
  }
  .page-header {
    border: 0;
    padding-bottom: 0;
  }
  .table {
    table-layout: fixed;
  }
  .selected-row {
    background: #EFF0F2 !important;
  }
  td {
    input[type="text"] {
      border: 0;
      width: 100%;
    }
  }
}

  ul.badge-status {
    padding: 0;
    margin: 0;
    list-style: none;
    .badge-status-status {
      width: 2em;
    }

    li.pending {
      color: #A4AAB4;
      font-style: italic;
    }
  }


.filter-pilots {
  .badge-data.badge-list {
    label {
      input {
        vertical-align: top;
      }
      span {
        display: inline-block;
        width: 210px;
        margin-right: -1em;
      }
    }
  }
}
