.pe-header {
  display: flex;
  border-bottom: 2px solid #eee;
  margin-top: 20px;
  margin-bottom: 20px;

  .pe-title {
    flex: 1;

    h4 {
      font-size: 18px;
      margin-bottom: 15px;
    }
  }

  .btn-add {
    background: $db-torquiose;
    font-size: 14px;
    text-transform: uppercase;
    color: white;
    width: 135px;
    border-radius: 2px;
    box-shadow: 1px 1px 0px #bbb;
  }
}

.pe-table {
  margin-top: 20px;
  width: 100%;

  tr {
    border-bottom: 1px solid #bbb;
  }

  th, td {
    padding: 10px;
  }

  th {
    color: #8D95A2;
  }

  td {
    color: $db-blue;
    width: 20%;
  }

  .settings-menu {
    border: 1px solid #eee;
    padding: 5px;
    border-radius: 2px;
  }

  .dropdown-menu {
    right: 0;
    left: inherit;
    text-align: center;
  }

}
