@keyframes fade-in {
  0% { opacity: 0; }
  50% { opacity: 0.5; }
  100% { opacity: 1; }
}

.header-pilot-name {
  font-size: 20px;
  color: $db-blue;
}

.header-pilot-number {
  color: grey;
}

.normal-field-label {
  color: #8E96A3;
}

.earnings-panel {
  position: absolute;
  top: -10px;
  right: 20px;

  span {
    padding-right: 20px;
  }
  .btn-danger {
    margin-left: 16px;
  }
}

.pilot-profile .section-header {
  padding: 5px 10px;
  background: #E2E4E7;
  font-family: roboto, arial, sans-serif;
  font-weight: bold;
  color: $db-blue;
  text-transform: uppercase;
}

.mg-bottom-sm {
  margin-bottom: 15px;
}

.mg-right-sm {
  margin-right: 15px;
}

.pilot-section {
  .pilot-section-col {
    padding: 15px;
    border-bottom: 1px dashed lightgray;
  }

  .license-col {
    height: 100px;
  }

  img {
    width: 115px;
    height: 75px;
  }
}

.mission-preference {
  .checked-checkbox, .unchecked-checkbox {
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 3px;
  }

  .checked-checkbox {
    background: #3FC0C2;
    color: white;
    text-align: center;
    font-size: 10px;
  }

  .unchecked-checkbox {
    border: 1px solid #8E96A3;
  }
}

.pilot-badge {
  border: 1px solid black;
  border-radius: 5px;
  padding:10px;
  display: flex;
  align-items: center;

  animation-name: fade-in;
  animation-duration: 0.5s;

  div:first-child {
    flex: 1;
  }

  span {
    color: gray;
  }
}
