.pilot-search-autocomplete {

  .accordion {
    border-bottom: 1px solid #BDBDBD;
    padding: 12px 0;
  }

  .accordion-first {
    border-top: 1px solid #BDBDBD;
  }

  .equipments {
    .col-md-label {
      width: 12.5%;
    }

    label {
      font-weight: normal;
    }

    .equipment-header {
      font-size: 15px;
      cursor: pointer;

      .arrow {
        img {
          width: 10px;
        }
      }

      .counter {
        color: #ccc
      }

    }

    .equipment-form {
      padding-top: 9px;
    }

  }
  .clear-button a {
    color: $db-turquoise-1;
    font-size: 12px;
  }
  .checkbox-filter {
    margin-top: 6px;
    input[type="checkbox"] {
      margin-right: 5px;
    }
  }


  .pilot-list {
    overflow: auto;
    border: 1px solid #CCC;
    padding: 0 10px;
    padding-bottom: 5px;

    > li {
      padding: 20px 10px;
      list-style: none;
      border-bottom: 1px solid #BDBDBD;
    }
  }
  .sort-by {
    margin-top: 20px;
  }

  .search-query {
    img {
      width: 18px;
      position: absolute;
      top: 6px;
      left: 28px;
    }

    input {
      padding: 10px 10px 10px 40px;
      border-radius: 6px;
    }
  }

  .search-query input, select {
    // padding: 10px;
    border-radius: 6px;
    font-size: 14px;
    max-height: 30px;
  }

}
