.info-window-panel {
  display: flex;
  width: 420px;
  margin: 10px;
  align-items: flex-end;
  font-family: roboto;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  .left-panel {
    max-width: 250px;
    .addresses {
      font-size: 18px;
      font-weight: 400;
      color: #1F2B45;
      margin-bottom: 20px;
    }
    .mission-details {
      font-size: 16px;
      font-weight: 300;
      color: #8D95A2;
    }
  }
  .right-panel {
    flex-grow: 2;
    align-items: flex-end;
    font-size: 16px;
    font-weight: 300;
    color: #8D95A2;

    display: flex;
    flex-direction: column;
    justify-content: end;
    text-align: right;
    color: #8D95A2;
    .btn-primary {
      margin-top: 10px;
      min-width: 80px;
      text-align: center
    }
  }
}

.map-canvas-dashboard {
  position: relative;
  width: 100%;
  height: 500px;
}
.available-mission, .client-mission, .pano-mission {
  .gm-style-iw {
    width: 430px !important;
  }
}
.mission-list.gm-style-iw {
  width: 450px !important;
  top: 15px !important;
  left: 0 !important;

  background-color: #fff;
  box-shadow: 0 1px 6px rgba(178, 178, 178, 0.6);
  border: none;
  border-image: none;
  border-radius: 5px;
  @media (max-width: $screen-xs-max) {
    width: 320px !important;
  }
}
#pano-infowindow {
  position: relative;
  width: 350px;
  height: 85px;
  @media (max-width: $screen-xs-max) {
    width: 200px;
    height: 80px;
  }
}
.marker-cluster {
  border-radius: 50%;
  min-width: 40px;
  min-height: 40px;
  padding-top: 6px;
  padding-left: 1px;
  font-size: 24px;
  text-align: center;
  color: white;
  background-color: rgba(242, 102, 73, 0.75);
  border: 2px solid #f26649;
}
.client-infowindow {
  width: 275px;
  padding: 15px;
  .address {
    color: #149b9e;
    font-weight: normal;
  }
}
.pano-infowindow-content {
  width: 275px;
  background-color: white;
  padding-top: 10px;
  padding-left: 15px;
  @media (max-width: $screen-xs-max) {
    width: 160px;
    padding-top: 5px;
    padding-left: 5px;
  }
}
.pano-infowindow-action {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #dfe8e2;
  width: 60px;
  padding-top: 16px;
  color: #777777;
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  line-height: 14px;
  @media (max-width: $screen-xs-max) {
    width: 40px;
    font-size: 10px;
    padding-top: 14px;
  }
}
.pano-infowindow-action-icon {
  font-size: 22px;
  padding-bottom: 4px;
  @media (max-width: $screen-xs-max) {
    font-size: 18px;
  }
}
.pano-infowindow-title {
   font-size: 14px;
   font-weight: 400;
   padding-bottom: 5px;
   background-color: white;
   color: #f26649;
   margin: 1px;
   border-radius: 2px 2px 0 0; /* In accordance with the rounding of the default infowindow corners. */
   @media (max-width: $screen-xs-max) {
     font-size: 12px;
   }
}
.pano-infowindow-icon {
  float: left;
  padding-right: 10px;
  font-size: 16px;
  color: #777777;
  @media (max-width: $screen-xs-max) {
    padding-right: 5px;
  }
}
.pano-infowindow-address {
  float: left;
  line-height: 16px;
  width: 230px;
  @media (max-width: $screen-xs-max) {
    width: 130px;
    line-height: 14px;
    font-size: 12px;
  }
}
.pano-infowindow-likelihood {
  float: left;
  width: 70%;
  margin-top: 6px;
  padding-left: 5px;
  padding-right: 10px;
  @media (max-width: $screen-xs-max) {
    margin-top: 8px;
  }
}
.likelihood-container {
  width: 100%;
  position: relative;
  height: 3px;
}
.likelihood-bg {
  width: 100%;
  background-color: #ddd;
  height: inherit;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  @media (max-width: $screen-xs-max) {
    width: 99%;
  }
}
.likelihood-bg-bar {
  width: 50%;
  background-color: #f26649;
  height: inherit;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}
.likelihood-space {
  position: relative;
  width: 20%;
  border-right: 2px solid white;
  height: inherit;
  float: left;
  z-index: 3;
}
.likelihood-info-icon {
  position: absolute;
  top: -5px;
  right: -15px;
  color: $gray;
  z-index: 4;
  &:hover, &:link, &:visited, &:active {
    text-decoration: none;
    color: $gray;
  }
}
