.flight-app {
  display: block;
  padding: 15px;
  .app-header {
    font-size: 18px;
    width: 50%;
    border-bottom: 1px solid #e5e6eb;
    cursor: pointer;
    padding: 5px;
    .arrow {
      margin-left: 5px;
      width: 15px;
      height: 20px;
      img {
        width: 100%;
      }
    }
  }
  .app-body {
    padding: 10px;
    margin: 10px 0;
    width: 50%;
    .header {
      width: 100%;
      border-bottom: 1px solid #e5e6eb;
      font-size: 12px;
      margin-bottom: 15px;
      .toggle-button {
        display: inline-block;
        width: 20%;
        padding: 8px;
        text-align: center;
        color: #c8c8c8;
        &.selected {
          border: 1px solid #e5e6eb;
          border-bottom-color: white;
          border-top-left-radius: 3px;
          border-top-right-radius: 3px;
          color: #327ab7;
          border-bottom-width: 2px;
          margin-bottom: -1px;
        }
      }
    }
    .content {
      margin: 15px 0;
      min-width: 300px;
      width: 80%;
      .label {
        font-weight: bold;
        font-size: 12px;
        color: black;
      }
      .CodeMirror {
        min-height: 120px;
        height: 120px;
        overflow-y: scroll;
      }
      .CodeMirror-scroll {
        min-height: 90px;
      }
    }
  }
}