.refine {
  position: fixed;
  height: calc(100vh - 50px);
  padding-left: 15px;
  width: 300px;
  background-color: #eee;
  padding-bottom: 30px;
  padding-right: 15px;
  &.over-flow-scroll {
    overflow: scroll;
  }
  .header-row {
    display: flex;
    justify-content: space-between;
    .filter-hidden-icon {
      width: 15px;
      height: 15px;
    }
  }
  .asset-status {
    font-family: roboto;
    label {
      font-weight: 200;
      input {
        margin-right: 5px;
      }
    }
  }
  .dropdown-refine {
    .refine-content {
      .dropdown-menu {
        display: block;
        .active a {
          background-color: #E1E3E7;
          color: #333333;
        }
      }
      .btn-primary {
        margin-top: 30px;
        margin-bottom: 10px;
      }
      .client-autocomplete {
        position: relative;
        width: 100%;
        padding-left: 15px;
        border: 1px solid #eee;
        height: 35px;
      }
      .client-autocomplete-list {
        position: absolute;
        background: white;
        width: 225px;
        border: 1px solid #eee;
      }
      .client-in-list {
        height: 30px;
        border-top: 1px solid #eee;
        display: flex;
        align-items: center;
        padding-left: 15px;
        cursor: pointer;
      }
      .client-in-list:hover {
        background: #eee;
      }
      .selected-client {
        border: 1px solid #3fc0c2;
        color: #3fc0c2;
        height: 25px;
        display: flex;
        align-items: center;
        width: fit-content;
        padding-left: 10px;
        padding-right: 10px;
        border-radius: 20px;
        margin-top: 5px;
        .fa {
          margin-left: 10px;
        }
      }
      .include-switch {
        display: flex;
        justify-content: space-between;
        margin-bottom: 15px;
        margin-top: 10px;
        font-size: 15px;
        font-family: roboto;
        font-weight: 200;
      }
      .switch {
        position: relative;
        display: inline-block;
        width: 47px;
        height: 20px;
      }

      /* Hide default HTML checkbox */
      .switch input {display:none;}

      /* The slider */
      .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #EFEFEF;
        -webkit-transition: .4s;
        transition: .4s;
      }

      .slider:before {
        position: absolute;
        content: "";
        height: 13px;
        width: 13px;
        left: 4px;
        bottom: 4px;
        background-color: white;
        -webkit-transition: .4s;
        transition: .4s;
      }

      input:checked + .slider {
        background-color: #3fc0c2;
      }

      input:focus + .slider {
        box-shadow: 0 0 1px #3fc0c2;
      }

      input:checked + .slider:before {
        -webkit-transform: translateX(26px);
        -ms-transform: translateX(26px);
        transform: translateX(26px);
      }

      /* Rounded sliders */
      .slider.round {
        border-radius: 34px;
      }

      .slider.round:before {
        border-radius: 50%;
      }
      .mission-status-filter {
        font-family: roboto;
        text-align: left;
        margin: 0;
        background-color: white;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        label {
          padding: 0;
          input {
            margin-right: 5px;
          }
        }
        .separator {
          border-bottom: 1px solid #eee;
          width: 100%;
          margin: 10px 0px 15px;
        }
      }

      .location-header {
        margin-bottom: 15px;
        margin-top: 10px;
        font-size: 15px;
        font-family: roboto;
        font-weight: 200;
      }
      .location-data {
        margin-bottom: 15px;
        input {
          width: 100%;
          padding-left: 15px;

          border: 1px solid #eee;
          height: 35px;
        }
      }
      .distance-slider {
        display: flex;
        margin-top: 10px;
        input {
          margin-left: 5px;
          margin-right: 5px;
        }
      }
    }
  }
}
