.update-button {
  margin-top: 15px;
}

.cyberduck-no-credentials {
  display: flex;
  height: 69px;
  align-items: flex-end;
}

.cyberduck-credentials {
  .error {
    display: none;
  }
}

.cyberduck-content {
  .cyberduck-link, .download-link {
    display: flex;
    align-items: center;
    color: #00c3c4;
    margin-bottom: 5px;

    img {
      margin-right: 5px;
    }
  }

  .cyberduck-link {
    span {
      text-decoration: underline;
    }
  }

  p {
    color: #9ba5b0;
    font-size: 12px;
  }
}
