.mission-status-filter {
  margin: 10px 0;
  text-align: center;

  label {
    font-weight: 200;
    padding: 5px 8px;
  }
}

.mission-reject-form {
  display: none;

  p {
    color: red;
    font-weight: bold;
  }
}

.main-body {
  display: flex;
  margin-left: -15px;
  .missions-list {
    width: 100%;
    margin-left: 320px;
    .page-header {
      margin-top: 20px;
    }
    .switch-with-label {
      float: right;
      margin-right: 15px;
      button {
        margin-left: 20px;
      }
    }
  }
  .no-filter {
    margin-left: 20px;
  }
  .filter-hidden-icon {
    width: 25px;
    height: 25px;
    margin-top: 25px;
    margin-left: 25px;
  }
}

.on-hold-warning {
  color: #FDA500;
  font-style: italic;
}
