.filter-pilots {
  overflow: auto;
  font-family: Roboto, sans-serif;
  background: #EFF0F2;

  .panel-header {
    margin-bottom: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc;
    display: flex;

    .panel-back {
      flex: 1;
    }

    .panel-clear {
      padding-top: 2px;

      a:hover {
        color: #149b9e;
        text-decoration: none;
      }
    }
  }

  .panel-body {
    padding-top: 0;

    label {
      font-weight: normal;
    }

    input[type="checkbox"] {
      margin-right: 5px;
    }
  }

  .panel-title {
    font-size: 16px;
    color: #337ab7;
    margin-top: 0;
  }

  .panel-section-title {
      font-weight: bold;
      font-size: 16px;
      margin-bottom: 10px;
      margin-top: 0px;
  }

  legend.panel-section-title {
    border-bottom: none;
  }

  .dropdown-refine {
    .refine-header {
      border-radius: 5px;

      i {
        padding-top: 3px;
      }
    }
  }

}
