.panel-group {
  .panel-heading {
    background-color: white;
    a:link {
      text-decoration: none;
    }
    a:visited {
      text-decoration: none;
    }
    .panel-title {
      color: $title-blue;
    }
    .asset-list {
      color: $gray;
    }
  }
  .pano-share, .pano-edit {
    float: left;
    margin-right: 5px;
  }
  .asset-list {
    .process-state {
      font-style: italic;
      font-size: 14px;
      color: #1D2D47;
      text-transform: capitalize;
      &.completed {
        color: #8D95A2;
      }
      &.error {
        color: #F26649;
      }
    }
  }
}
.placeholder.aseet-is-processing {
  width: 25%;
  height: 120px;
  position: relative;
  border: 2px solid #E1E3E7;
  margin-bottom: 20px;
  font-size: 20px;
  &.panorama_stitching {
    width: 50%;
  }
  .placeholder-wrapper {
    position: absolute;
    left: calc(50% - 37px);
    top: 20px;
  }
  .note {}
  .loader-animation {
    width: 50px;
    height: 50px;
    margin: auto;
    border: 5px solid #EEEEEE; /* Light grey */
    border-top: 5px solid #00B5B5; /* turquoise */
    border-radius: 50%;
    animation: spin 2s linear infinite;
  }
}
.pano-asset{
  width: 50%;
  margin-left: -5px;
  position: relative;
  .panorama {
    height: 150px;
    border: 3px solid #E1E3E7;
    &.asset-final {
      border-color: #27BD15;
    }
  }

  .actions {
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.16);
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    text-align: right;
    padding: 2px 10px;

    .asset-share, .promote-asset {
      float: left;
      margin-left: 10px;
    }

    a {
      color: #fff;
      font-size: 20px;
      transition: opacity .3s ease-in-out;
      -moz-transition: opacity .3s ease-in-out;
      -webkit-transition: opacity .3s ease-in-out;

      &.download-asset {
        float: left;
      }
    }
  }
  &:hover {
    .actions {
      opacity: 1.0;
      transition: opacity .3s ease-in-out;
      -moz-transition: opacity .3s ease-in-out;
      -webkit-transition: opacity .3s ease-in-out;
    }
  }
  //the only way that I found to hide controls at pano-viewer
  .panorama div div:first-child div:nth-child(2) div:nth-child(2) {
    display: none;
  }
}
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.panel-body {
  .button-width {
    width: 250px;
  }

  .button {
    color: #356B90;

    img {
      width: 20px;
    }
  }

  .button-mg-right {
    margin-right: 20px;
  }

  .dropdown-menu li a {
    font-weight: 300;
    font-size: 13px;
    color: #1D2D47;
  }
}

.gps-data-missing {
  float: right;
  color: #fec331;
}

.asset-gps-data-missing {
  border: 3px solid #fec331 !important;
}

.action-menu:active, .action-menu:hover, .action-menu:focus {
  @extend .btn-secondary:hover;
}

.asset-warning {
  text-align: right;
  height: 20px;
  i {
    color: #fec331;
  }

  span {
    color: grey;
    text-decoration: underline;
    font-style: italic;
  }
}
.regenerate-zip {
  line-height: 30px;
  margin: 5px 0 7px;
  font-size: 12px;
  color: #ababab;
  padding-left: 20px;
  font-style: italic;
  .buttons-block {
    float: right;
    font-style: normal;
    .btn-primary {
      margin-right: 10px;
    }
  }
  .regenerating {
    color: $gray;
    background-color: #fafafa;
    border-color: #fafafa;
    display: inline-block;
    padding: 10px;
    margin-top: -11px;
    .spinner {
      display: inline-block;
      width: 15px;
      height: 15px;
    }
  }
}

.processing-status {
  .error {
    color: #F26649;
  }

  .completed {
    color: #8D95A2;
  }

  .processing, .queued {
    color: #1D2D47;
  }
}
