.modal-close {
  margin: 15px;
  float: right;

  img {
    width: 15px;
  }
}

.rating-modal-content {
  span {
    font-size: 16px;
  }
}

.capacity-modal .capacity-modal-content, .reschedule-modal .reschedule-modal-content {
  .row {
    .nav-dates {
      border-bottom: 3px solid #ddd;
      width: 100%;
      padding-bottom: 10px;
      .btn {
        width: 80px;
      }
    }
  }
  .header-label {
    margin-top: -15px;
    margin-bottom: 40px;
    p {
      font-weight: 200;
      margin: 0 0 4px;
      height: 22px;
    }
    img {
      width: 26px;
    }
  }
  .table {
    td {
      padding: 1px;
      border-bottom: 1px solid #ddd;
      text-align: center;
    }
    th {
      padding: 10px;
      text-align: center;
      border-bottom: 1px solid #ddd;
      &.time {
        width: 90px;
      }
      .day {
        font-weight: 400;
      }
      p.header {
        margin-top: -10px;
        font-weight: 400;
        &.date {
          font-weight: 200;
          font-size: 24px;
        }
      }
      &.unavailable {
        border-radius: 3px;
        background-color: #F2F3F5;
      }
    }

    td.capacity {
      width: 235px;
      .capacity-details {
        height: 57px;
      }

      .high, .medium, .low, .popup {
        p {
          font-size: 12px;
          font-weight: 200;
        }
        span {
          font-weight: 400;
          font-size: 14px;
        }
      }
      .high, .medium, .low {
        padding: 5px 0px;
        border-radius: 3px;
        .popup {
          padding: 8px;
          margin-left: -260px;
          margin-top: -5px;
          border-radius: 3px;
          box-shadow: #000000 0 1px 6px;
          display: inline-block;
          background-color: white;
          position: absolute;
          height: auto;
          z-index: 10;
          p {
            text-align: left;
          }
        }
        .capacity-details-basic {
          margin-top: 8px;
          img {
            width: 30px;
            margin-top: 10px;
          }
          p {
            float: left;
            margin: 2px;
            padding-left: 30px;
          }
          span {
            margin-top: 1px;
            float: right;
          }
        }
      }
      .unavailable {
        padding: 6px 10px;
        background-color: #F2F3F5;
        border-radius: 3px;
        p {
          text-align: center;
          font-size: 16px;
          margin-bottom: 4px;
          &.small {
            font-weight: 200;
            font-size: 12px;
            margin: 0;
          }
        }
      }
      &.selected {
        background-color: #149B9E;
        box-shadow: #000000 0 1px 6px;
        border-radius: 3px;
      }
      .high {
        background-color: #D8F2F2;
      }
      .medium {
        background-color: #FDE7E3;
      }
      .low {
        background-color: #FFEA9A;
      }
    }

  }
  .nav-dates {
    display: inline-flex;
    span {
      font-size: 18px;
    }
    img {
      margin: 5px 8px;
      height: 16px;
    }
  }
}

.client-package-modal {
  .trigger {
    padding-top: 7px;
  }
  .error {
    font-size: 12px;
  }
  .row {
    margin-bottom: 15px;
  }
  .form-group {
    margin-bottom: 0;
  }
  label, .slider-label {
    font-size: 14px;
    color: #1D2D47;
    font-weight: 400;
  }
  .grey-section {
    background-color: #F6F7F8;
    padding: 20px 10px;
    margin-left: 2px;
    margin-right: 2px;
    margin-top: 30px;
    span {
     color: #8D95A2;
     font-size: 12px;
    }
  }
  .dispatch-section {
    .section-header {
      display: flex;
      font-size: 20px;
      background: white;
      padding: 7px 0;
      border-bottom: 2px solid #C5C9D0;
      margin-bottom: 15px;
      text-transform: none;
      justify-content: space-between;
    }
    .row.without-margin {
      margin-right: 0;
      margin-left: 0;
    }
  }
  .badge-required {
    padding-top: 36px;
    color: #ababab;
    font-weight: 400;
    display: inline-block;
    span {
      margin-left: 3px;
    }
  }

  .switch-with-label {
    color: #ababab;
    font-weight: 400;
    span.slider-label {
      padding-left: 15px;
      margin-top: -5px
    }
    label {
      margin-left: 5px;
    }
  }
}

.modal-content.reschedule-modal {
  margin-left: -250px !important;
  overflow-y: inherit;
  .radiogroup [role='radio'] {
    margin: 20px 10px 20px 0;
    font-weight: 100;
    display: inline-block;
  }
  select.form-control {
    color: #1d2d47
  }
  input, select {
    margin-bottom: 10px;
  }
  .col-md-12.top-seperator{
    border-top: 2px solid #eee;
    margin-top: 10px;
    padding-left: 0;
  }
  label {
    font-weight: 100;
    font-size: 14px;
  }
}

.modal-footer.buttons-block {
  text-align: left;
}


.capacity-modal-content, .hold-modal-content, .reshoot-modal-content, .rating-modal-content, .reschedule-modal-content {
  padding: 30px 40px;
  color: #444c61;

  .stars {
    p {
      display: inline-block;
      padding: 0 7px;
      margin-top: -20px;
    }
    .star_container {
      display: inline-block;
      width: 120px;
      vertical-align: top;
    }
    .star {
      border-radius: 50%;
      height: 55px;
      margin: 50px auto;
      width: 48px;
      padding: 25px;
      transition: all 600ms;
      background-repeat: no-repeat;
      &:hover, &.selected {
        transform: scale(2);
      }
    }
    .star1 {
      background-image: url(images/pilot_rating/1_stars_gray.svg);
      &:hover, &.selected {
        background-image: url(images/pilot_rating/1_stars_selected.svg);
      }
    }
    .star2 {
      background-image: url(images/pilot_rating/2_stars_gray.svg);
      &:hover, &.selected {
        background-image: url(images/pilot_rating/2_stars_selected.svg);
      }
    }
    .star3 {
      background-image: url(images/pilot_rating/3_stars_gray.svg);
      &:hover, &.selected {
        background-image: url(images/pilot_rating/3_stars_selected.svg);
      }
    }
    .star4 {
      background-image: url(images/pilot_rating/4_stars_gray.svg);
      &:hover, &.selected {
        background-image: url(images/pilot_rating/4_stars_selected.svg);
      }
    }
    .star5 {
      background-image: url(images/pilot_rating/5_stars_gray.svg);
      &:hover, &.selected {
        background-image: url(images/pilot_rating/5_stars_selected.svg);
      }
    }
  }


  label {
    font-size: 16px;
  }

  .buttons-block {
    a.btn:first-child {
      margin-right: 16px;
    }
  }

  .error-message {
    margin: 5px 0;
    color: #f26649;
    display: none;
  }

  .checkbox-label {
    font-weight: normal;
  }

  h3 {
    margin-top: -5px;
  }

  textarea {
    height: 100px;
    border-radius: 3px;
    border: 1px solid #ccc;
  }

  input[type="radio"] {
    margin-right: 5px;
  }
}
.edit-client-profile-modal, .client-package-modal {
  .buttons-block {
    margin-top: 20px;
  }
  .timeframe {
    width: 50%;
    float: left;
  }
}
